import React, {memo, Suspense, useEffect, useMemo} from "react";
import {Route, Routes} from "react-router-dom";
import ErrorBoundary from "../common/ErrorBoundary/ErrorBoundary";
import Tasks from "../Tasks/Tasks";
import Home from "../Home/Home";
import classes from "./ContentContainer.module.css"
import {useLocation} from "react-router";
import {useDispatch, useSelector} from "react-redux";
import type {} from 'redux-thunk/extend-redux';
import {AppDispatch, GlobalStateType} from "../../redux/store-redux";
import {appActions} from "../../redux/app-reducer";
import ErrorToastContainer from "../common/NotifyToast/NotifyToastContainer";

const UsersContainer = React.lazy( () => import("../users/UsersContainerFC") )
const ProfileContainer = React.lazy( () => import("../Profile/ProfileContainerFC") )
const Dialog2Container = React.lazy( () => import("../Dialog2Messages2/Dialog2Messages2Container") )
const LoginContainer = React.lazy( () => import("../Login/LoginContainer") )
const News = React.lazy( () => import("../News/News") )
const Rest = React.lazy( () => import("../Rest/Krestiki-Noliki/KrestikiNoliki") )
const StackInfo = React.lazy( () => import("../Info/StackInfoBS") )
const FeedBackContainer = React.lazy( () => import("../FeedBack/FeedBackContainer") )

const ContentContainer: React.FC = memo( () => { //      
    console.log("ContentContainer")
    const {setPatch, setPageWidth} = appActions
    const Patch: string = useSelector((state:GlobalStateType) => state.app.patch)

    const dispatch = useDispatch<AppDispatch>();

    const location = useLocation()
    useEffect( () => { //          
        const patch2 = location.pathname //   URL  /profile
            .split( "" ) //     ['/', 'd', 'i', 'a', 'l', 'o', 'g', 's', '/', '2', '8', '8', '3', '1',]
        const Aaa: Array<String> = [] //   
        for (let i: number = 1; i < patch2.length; i++) { //    ,    '/'
            if (patch2[i] === '/') {
                break //  ,   /
            }
            Aaa.push( patch2[i] ) //    
        }

        const UpdatedPatch: string = Aaa.join( "" ) //  
        if (Patch!== UpdatedPatch) {
            dispatch( setPatch( UpdatedPatch ))
            //    patch  app-reducer
        }

    }, [location, setPatch, dispatch, Patch] )

    const setPageWidthLocal  = () => { //    
        const pageWidth1 = document.documentElement.scrollWidth//    
        setTimeout( () => { //  
            const pageWidth2 = document.documentElement.scrollWidth //     
            if (pageWidth1 === pageWidth2) { //        ,
               dispatch(setPageWidth( pageWidth1 )) //    (    )
            }
        }, 300 ) //      
    }

    window.onresize = setPageWidthLocal;

    return (<div>
        {useMemo(()=><ErrorToastContainer/>,[]) }{/*    200 */}
        <ErrorBoundary> {/*   ContentContainer*/}
            <Suspense fallback={
                <div>...</div>}> {/* ,    Lazy import   fallback   */}
                <div className={classes.contentContainer}>
                    <Routes> {/*   URL   */}
                        <Route path='' element={<Home/>}/> {/* */}
                        <Route path='/mystack/*' element={<StackInfo/>}/> {/* */}
                        <Route path='/profile/*' element={<ProfileContainer/>}/> {/**/}
                        <Route path='/dialog2/*' element={<Dialog2Container/>}/> {/**/}
                        <Route path='/messages/*' element={<Dialog2Container/>}/> {/**/}
                        <Route path='/users/*' element={<UsersContainer/>}/> {/*  Users*/}
                        <Route path='/login/*' element={<LoginContainer/>}/> {/**/}
                        <Route path='/news/*' element={<News/>}/> {/*   hn algonia*/}
                        <Route path='/rest/*' element={<Rest/>}/> {/* */}
                        <Route path='/feedback/*' element={<FeedBackContainer/>}/> {/* */}
                        <Route path='/tasks/*' element={<Tasks/>}/> {/* */}
                    </Routes>
                </div>
            </Suspense>
        </ErrorBoundary>
    </div>)
})
export default ContentContainer
