import React, {memo} from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import {ModalBodyType, ModalHeaderType} from "../types/commonTypes";

type ModalBS1Type = {
    show: boolean,//     
    buttonName: string // ,   
    modalHeader: ModalHeaderType,// JSX    
    modalBody: ModalBodyType, // JSX    
    setShow: (show: boolean)=>void, //   show (    )
    buttonOnClick: () => void, //       
}
const ModalBS1:React.FC<ModalBS1Type> = memo ( ({show, setShow, modalHeader, modalBody, buttonOnClick, buttonName}) => {

    const localAction = () => {
        setShow(false)
        buttonOnClick()
    }

    return (
        <>
            <Modal show={show} onHide={()=>{setShow(false)}}  size="lg">
                <Modal.Header closeButton>
                    <Modal.Title>{modalHeader}</Modal.Title>
                </Modal.Header>
                <Modal.Body>{modalBody}</Modal.Body>
                <Modal.Footer>
                    <Button variant="primary" onClick={localAction}>
                        {buttonName}
                    </Button>
                </Modal.Footer>
            </Modal>
        </>
    );
})

export default ModalBS1
