import {apiProfile} from "../components/api/api";
import {profileActions} from "./profile-reducer";
import {usersActions} from "./users-reducer";
import {InferActionsTypes} from "./store-redux";
import {GetProfileType} from "../components/api/apiTypes";
import {ResultCodeEnum, ResultCodeEnumCaptcha} from "../components/api/enum";
import {ComThunkTp, NulableType} from "../components/common/types/commonTypes";
import {dialog2Actions} from "./dialog2-reducer";
import {appActions} from "./app-reducer";

const SET_MY_DATA = "myApp/auth-reducer/SET_MY_DATA"; //        (ID, Email, login, isAuth)
const AUTH_INITIAL_STATE = "myApp/auth-reducer/AUTH_INITIAL_STATE"; //   
const SET_CAPTCHA_URL = "myApp/auth-reducer/SET_CAPTCHA_URL"; //  URL 
const SET_LOGIN_ERROR = "myApp/auth-reducer/SET_LOGIN_ERROR"; //   
const SET_MY_PROFILE = "myApp/auth-reducer/SET_MY_PROFILE"; //      

type SetAuthDataActionType = {
    type: typeof SET_MY_DATA,
    id: number,
    email: string,
    login: string,
    isAuth: boolean
}

export const authActions = {
    setMyProfile: (myProfile: GetProfileType) => { //       
        return {type: SET_MY_PROFILE, myProfile} as const
    },

    setAuthData: (id: number, email: string, login: string, isAuth: boolean): SetAuthDataActionType => {
        return {type: SET_MY_DATA, id, email, login, isAuth} as const
    },

    authInitialState: () => { //     
        return {type: AUTH_INITIAL_STATE} as const
    },

    setCaptchaURL: (captchaURL: string) => { //    URL    
        return {type: SET_CAPTCHA_URL, captchaURL} as const
    },

    setLoginError: (loginError: string) => { //      
        return {type: SET_LOGIN_ERROR, loginError} as const
    }
}

export type AuthActionTypes =
    InferActionsTypes<typeof authActions> |
    InferActionsTypes<typeof profileActions> |
    InferActionsTypes<typeof usersActions> |
    InferActionsTypes<typeof dialog2Actions> |
    InferActionsTypes<typeof appActions>

const initialState = { //      
    myId: 0 as number, //  ID  
    myEmail: "" as string,//  Email  
    myLogin: "" as string,//    
    isAuth: false, //  
    myProfile: null as NulableType<GetProfileType>, //     
    captchaURL: "" as string, // URL   5  
    loginError: "" as string, //    
}
type InitialStateAuthType = typeof initialState
const authReducer = (state: InitialStateAuthType = initialState, action: AuthActionTypes): InitialStateAuthType => { //     
    let stateCopy: InitialStateAuthType; //      
    switch (action.type) {
        case SET_MY_DATA: //    id, email, login
            stateCopy = {
                ...state,
                myId: action.id,
                myEmail: action.email,
                myLogin: action.login,
                isAuth: action.isAuth,
            }
            return stateCopy; //     
        case SET_MY_PROFILE: //     
            stateCopy = {
                ...state,
                myProfile: action.myProfile
            }

            return stateCopy; //     
        case AUTH_INITIAL_STATE: //    
            stateCopy = initialState
            return stateCopy; //     
        case SET_CAPTCHA_URL: //   URL   
            stateCopy = {
                ...state,
                captchaURL: action.captchaURL
            }
            return stateCopy; //     
        case SET_LOGIN_ERROR: //      
            stateCopy = {
                ...state,
                loginError: action.loginError
            }

            return stateCopy; //     
        default:
            return state; //     
    }
}

export const getAuthMeThunkCreator = (): ComThunkTp<AuthActionTypes> => {//  ?.    
    return async (dispatch, getState) => {
        const response = await apiProfile.getAuthMe() //  ?
        if (response.resultCode === ResultCodeEnum.Success) { //   /
            const {id, email, login} = response.data //  ID, , 
            dispatch( authActions.setAuthData(
                id, email, login, //     ID, , 
                true //    
            ) )
            const response2 = await apiProfile.getProfile( response.data.id )//     

            dispatch( authActions.setMyProfile( response2 ) )//     
            if (response.resultCode !== ResultCodeEnum.Success) { //  
                dispatch( authActions.authInitialState() ) //   
            }
        }
    };
}

export const postLoginThunkCreator = (email: string, password: string, rememberme?: boolean, captcha?: string): ComThunkTp<AuthActionTypes> => {
    //  
    return async (dispatch, getState) => { //    
        const response = await apiProfile.postLogin( email, password, rememberme, captcha ) //       
        if (response.resultCode === ResultCodeEnum.Success) { //     
            dispatch( getAuthMeThunkCreator() ) //      
        } else { //      
            const message =  //    message -   
                !response.messages[0] //     
                    ? "no responce from server" //   
                    : response.messages[0] //     
            if (response.resultCode === ResultCodeEnumCaptcha.CaptchaIsReqiured) { //         
                dispatch( getCaptchaThunkCreator() )
            }
            dispatch( authActions.setLoginError( message ) ) //    
        }
    };
}

export const deleteLoginThunkCreator = (): ComThunkTp<AuthActionTypes> => {//  
    return async (dispatch, getState) => { //    
        const response = await apiProfile.deleteLogin() //    
        if (response.resultCode === ResultCodeEnum.Success) { //    
            setTimeout( () => {

                dispatch( authActions.authInitialState() )//    

                dispatch( profileActions.profileInitialState() )//    

                dispatch( usersActions.usersInitialState() )//  Users  

                dispatch( dialog2Actions.setDialog2InitialState() )//  Dialog2  

            }, 300 )
        } else {
            console.log( response.messages ) //      
        }
    };
}

export const getCaptchaThunkCreator = (): ComThunkTp<AuthActionTypes> => {//   
    return async (dispatch, getState) => { //    
        const response2 = await apiProfile.getCaptcha() //  
        dispatch( authActions.setCaptchaURL( response2.url ) ) //      
    };
}

export default authReducer;










