import {apiProfile} from "../components/api/api";
import {authActions} from "./auth-reducer";
import {ComThunkTp, NulableType, PostsType, ProfileType} from "../components/common/types/commonTypes";
import {InferActionsTypes} from "./store-redux";
import {GetProfileType} from "../components/api/apiTypes";
import {ResultCodeEnum} from "../components/api/enum";
import {appActions} from "./app-reducer";
import {saveDataToNotify} from "../components/common/functions/commonFunctions";

const SET_EDIT_PROFILE_ERROR = "myApp/auth-reducer/SET_EDIT_PROFILE_ERROR"; //    
const DELETE_POST = "myApp/profile-reducer/DELETE_POST";//    
const ADD_POST = "myApp/profile-reducer/ADD-POST";//    
const SET_USER_PROFILE = "myApp/profile-reducer/SET_USER_PROFILE"; //        
const SET_STATUS = "myApp/profile-reducer/SET_STATUS" //    
const PROFILE_INITIAL_STATE = "myApp/profile-reducer/PROFILE_INITIAL_STATE" //    

export const profileActions = {
    setEditProfileStatus: (editProfileStatus: Array<string>) => { //           
        return {type: SET_EDIT_PROFILE_ERROR, editProfileStatus} as const
    },

    deletePostActionCreator: (postId: number) => { //     postId
        return {type: DELETE_POST, postId} as const
    },

    addPostActionCreator: (newPostData: string) => { //   
        return {type: ADD_POST, newPostData} as const
    },

    setUserProfile: (profile: GetProfileType) => { //        
        return {type: SET_USER_PROFILE, profile} as const
    },

    setStatus: (newStatus: string) => { //    ( API )
        return {type: SET_STATUS, newStatus} as const
    },

    profileInitialState: () => { //   
        return {type: PROFILE_INITIAL_STATE} as const
    },
}

type ProfileActionTypes = InferActionsTypes<typeof profileActions> | InferActionsTypes<typeof authActions>
    | InferActionsTypes<typeof appActions>

const initialState = {
    posts: [//     
        {id: 1, message: "state 2 Hi, how are you?", like: 12},
        {id: 2, message: "state 2 it's, my first post", like: 15},
    ] as Array<PostsType>,
    profile: null as NulableType<GetProfileType>, //      
    status: "", //      
    editProfileStatus: [] as Array<string>, //       
}

type InitialStateType = typeof initialState

export const profileReducer = (state: InitialStateType = initialState, action: ProfileActionTypes): InitialStateType => { //  
    let stateCopy: InitialStateType; //      
    switch (action.type) {
        case SET_USER_PROFILE: //       
            stateCopy = {
                ...state,
                profile: action.profile //     
            }
            return stateCopy;
        case ADD_POST: {//  
            const newPost: PostsType = { //     
                id: 5, // id    ()
                message: action.newPostData, //     
                like: 2 //   ()
            };
            stateCopy = {
                ...state,
                posts: [...state.posts, newPost], //      
            }
            return stateCopy;

        }
        case DELETE_POST: {//    postId
            stateCopy = {
                ...state,
                posts: [...state.posts.filter( f => f.id !== action.postId )], //     
            }
            return stateCopy;
        }
        case SET_STATUS: //      ( API )
            stateCopy = {
                ...state,
                status: action.newStatus
            }
            return stateCopy;
        case PROFILE_INITIAL_STATE: //  
            stateCopy = initialState
            return stateCopy;
        case SET_EDIT_PROFILE_ERROR: //          
            stateCopy = {
                ...state,
                editProfileStatus: action.editProfileStatus
            }
            return stateCopy; //     
        default:
            return state;
    }
}

export const getProfileThunkCreator = (userId: number): ComThunkTp<ProfileActionTypes> => { //      
    return async (dispatch, getState) => { //       
        dispatch( appActions.toggleIsFetching( true ) ) //    

        const idLocal = !userId? getState().auth.myId : userId //  userId    URL (       ID  )
        const response = await apiProfile.getProfile( idLocal) //      

        dispatch( profileActions.setUserProfile( response ) ) //     
        !getState().profilePage.status && dispatch( getStatusThunkCreator( idLocal ) ) //   ,   
        dispatch( appActions.toggleIsFetching( false ) ) //    
    }
}

export const getStatusThunkCreator = (userId: number): ComThunkTp<ProfileActionTypes> => {  //     
    return async (dispatch, getState) => { //      
        const response = await apiProfile.getStatus( userId ) // api     userId
        dispatch( profileActions.setStatus( response ) ) //        
    }
}
export const putStatusThunkCreator = (statusTmpInput: string): ComThunkTp<ProfileActionTypes> => { //    
    return async (dispatch, getState) => { //     
        const response = await apiProfile.putStatus( statusTmpInput ) //     
        if (response.resultCode === ResultCodeEnum.Success) { //      
            dispatch( getStatusThunkCreator( getState().auth.myId ) )//       
        }
    }
}
export const setprofilePhotoThunkCreator = (profilePhoto: File): ComThunkTp<ProfileActionTypes> => { //     
    return async (dispatch, getState) => { //      
        dispatch( appActions.toggleIsFetching( true ) ) //    
        const response = await apiProfile.putPhoto( profilePhoto ) //     
        if (response.resultCode === ResultCodeEnum.Success) { //      
            dispatch (getProfileThunkCreator(getState().auth.myId)) //       
        }
    }
}

export const putMyProfileThunkCreator = (MyProfile: ProfileType): ComThunkTp<ProfileActionTypes> => { //     myProfile
    return async (dispatch, getState) => { //      myProfile
        const response = await apiProfile.putMyProfileData( MyProfile ) //     
        if (response.resultCode === ResultCodeEnum.Success) { //      
            const response2 = await apiProfile.getProfile( getState().auth.myId )//       
            dispatch( authActions.setMyProfile( response2 ) )//     
            dispatch( getProfileThunkCreator( getState().auth.myId ) )
            saveDataToNotify("Edited successfully!", "Success") //   -  
            dispatch(profileActions.setEditProfileStatus([])) //    

        } else { //         
            const message =  //    message -   
                response.messages && response.messages.length !== 0  //  response.messages       0
                    ? response.messages //     
                    : ["no responce from server"] //    
            dispatch( profileActions.setEditProfileStatus( message ) ) //     
        }
    }
}

export default profileReducer;










