import {getAuthMeThunkCreator} from "./auth-reducer";
import {getThemeThunkCreator} from "./theme-reducer";
import { InferActionsTypes} from "./store-redux";
import {ComThunkTp, NotifyType, ErrorType} from "../components/common/types/commonTypes";

const SET_INITIALISED_APP = "myApp/app-reducer/SET_INITIALISED_APP"; //  
const APP_INITIAL_STATE = "myApp/app-reducer/APP_INITIAL_STATE"; //   
const SET_PATCH = "myApp/app-reducer/SET_PATCH"; //    URL
const SET_PAGE_WIDTH = "myApp/app-reducer/SET_PAGE_WIDTH"; //   
const TOGGLE_IS_FETCHING = "myApp/users-reducer/TOGGLE_IS_FETCHING";
const SET_ERROR_GLOBAL = "myApp/users-reducer/SET_ERROR_GLOBAL";//      (  200 )
const SET_ERROR_200 = "myApp/users-reducer/SET_ERROR_200";//     (  200)
const SET_ERROR_200_ARCHIVE = "myApp/users-reducer/SET_ERROR_200_ARCHIVE";//      (  200)

export const appActions = {
    setInitialisedApp: () => { //     
        return {type: SET_INITIALISED_APP} as const
    },

    appInitialState: () => { //    
        return {type: APP_INITIAL_STATE} as const
    },

    setPatch: (patch: string) => { //    
        return {type: SET_PATCH, patch} as const
    },

    setPageWidth: (pageWidth: number) => { //    
        return {type: SET_PAGE_WIDTH, pageWidth} as const
    },
    toggleIsFetching: (isFetching: boolean) => {
        return {type: TOGGLE_IS_FETCHING, isFetching} as const
    },
    setAppErrorAC: (errorGlobal: ErrorType) => { //   (  200 )
        return {type: SET_ERROR_GLOBAL, errorGlobal} as const
    },
    setNotify: (notify: Array<NotifyType>) => { //    (  200)
        return {type: SET_ERROR_200, notify} as const
    },
    setNotifyArchive: (notifyItem: NotifyType) => { //     (  200)
        return {type: SET_ERROR_200_ARCHIVE, notifyItem} as const
    }
}

type AppActionTypes = InferActionsTypes<typeof appActions>

type InitialStateType = typeof initialState

const initialState = { //     
    initialisedApp: false, //   ?
    patch: "", //    URL
    pageWidth: document.documentElement.scrollWidth, //    
    mobileWidth: 620,
    isFetching: false, //   ()
    errorGlobal: {} as ErrorType,//   (  200 )
    notify: [] as Array<NotifyType>, //      (  200)
    notifyArchive: [] as Array<NotifyType> //   200
}

const appReducer = (state: InitialStateType = initialState, action: AppActionTypes): InitialStateType => {//  
    let stateCopy: InitialStateType; //      
    switch (action.type) {
        case SET_INITIALISED_APP: //   
            stateCopy = {
                ...state, //   
                initialisedApp: true, //      true
            }
            return stateCopy; //     
        case APP_INITIAL_STATE: //    
            stateCopy = initialState
            return stateCopy; //     
        case SET_PATCH: //   
            stateCopy = {
                ...state, //   
                patch: action.patch, //      true
            }
            return stateCopy; //     
        case TOGGLE_IS_FETCHING:
            stateCopy = {
                ...state,
                isFetching: action.isFetching
            }
            return stateCopy; //     
        case SET_PAGE_WIDTH: //    
            stateCopy = {
                ...state, //   
                pageWidth: action.pageWidth, //      true
            }
            return stateCopy; //     
        case SET_ERROR_GLOBAL: //       (  200 )
            stateCopy = {
                ...state, //   
                errorGlobal: action.errorGlobal,
            }
            return stateCopy; //     
        case SET_ERROR_200: //      ( 200 )
            stateCopy = {
                ...state, //   
                notify: action.notify,
            }
            return stateCopy; //     
        case SET_ERROR_200_ARCHIVE: //      ( 200 )  
            stateCopy = {
                ...state, //   
                //       
                notify: state.notify.filter((item: NotifyType)=> item.timeUnix!==action.notifyItem.timeUnix ), //
                //        
                notifyArchive: [...state.notifyArchive, action.notifyItem],
            }
            return stateCopy; //     
        default:
            return state; //     
    }
}

export const initialisedAppThunkCreator = (): ComThunkTp<AppActionTypes> => {//   
    return (dispatch, getState) => { //    
        const promise1 = dispatch( getAuthMeThunkCreator() ) //   
        const promise2 = dispatch( getThemeThunkCreator() ) //  
        Promise.all( [promise1, promise2] ) //    
            .then( () => {
                dispatch( appActions.setInitialisedApp() ) //     true
            } )
    };
}

export default appReducer;










