import {postFeedBack22} from "../components/api/api";
import {ApiFeedBackDataType} from "../components/common/types/commonTypes";
import {Dispatch} from "redux";
import {GlobalStateType, InferActionsTypes} from "./store-redux";

const SET_FEED_BACK_STATUS = "myApp/feedback-reducer/SET_FEED_BACK_STATUS"; //     feedback

export const feedBackActions = {
    setFeedBackStatus: (feedBackStatus: string) => { //    feedBackStatus
        return {type: SET_FEED_BACK_STATUS, feedBackStatus} as const
    }
}

type InitialStateType = { feedBackStatus: string }
const initialState: InitialStateType = { //   
    feedBackStatus: "" //    (feedBack) -       
}

type FeedBackActionTypes = InferActionsTypes<typeof feedBackActions>

const feedBackReducer = (state: InitialStateType = initialState, action: FeedBackActionTypes): InitialStateType => {//  
    let stateCopy: InitialStateType; //      
    switch (action.type) {
        case SET_FEED_BACK_STATUS: //   feedBackStatus
            stateCopy = {
                ...state,
                feedBackStatus: action.feedBackStatus
            }
            return stateCopy; //     
        default:
            return state; //     
    }
}

export const postFeedBackThunkCreator2 = (data: ApiFeedBackDataType) => {//   
    return async (dispatch: Dispatch<FeedBackActionTypes>, getState: () => GlobalStateType) => { //   
        await postFeedBack22( data ) //
            .then( () => dispatch( feedBackActions.setFeedBackStatus( "    " ) ) )//    BLL "    "
            .catch( (err: object) => dispatch( feedBackActions.setFeedBackStatus( (err.toString()) ) ) );//          BLL
    };
}

export default feedBackReducer;
