const tasksReducer = (state: InitialStateType = initialState, action: TasksActionTypes): InitialStateType => {// 
    let stateCopy: InitialStateType; //      
    switch (action.type) {
        case SET_NEWS_DATA: //   News
            stateCopy = {
                ...state, //   
                newsData: action.newsData,
            }
            return stateCopy; //     
        default:
            return state; //     
    }
}
export const getNewsThunkCreator = (query: string): ComThunkTp<TasksActionTypes> => {//    
    return async (dispatch, getState) => { // 
        const response: Array<HitsItemType> = await apiTasks.getNews( query );
        dispatch( tasksActions.setNewsDataAC( {...getState().tasks.newsData, serverData: response, query: query} ) ) //      
    };
}

export default tasksReducer;
